import {mapActions, mapState} from 'vuex'
import CateGorys from '../cateGorys/cateGorys.vue'
export default {
	components: {
		CateGorys
	},
	mounted() {
		this.getIndexDataAction()
	},
	methods: {
		...mapActions({
			getIndexDataAction: 'getIndexDataAction'
		})
	},
	computed: {
		...mapState({
			indexData: state => state.indexData.indexData
		})
	}
}
